import axios from "axios";
import { useEffect, useState } from "react";
import LikeSvg from "../../assets/svgs/LikeSvg";
import "./Post.css";
import { Link } from "react-router-dom";

const Post = ({ post, user }) => {
  const [authorName, setAuthorName] = useState("");
  const [profilePicture, setProfilePicture] = useState("");

  const [hasLiked, setHasLiked] = useState(false);
  const [numberOfLikes, setNumberOfLikes] = useState(post.numberOfLikes);

  useEffect(() => {
    axios
      .get(
        "https://mern-blog-backend-api.herokuapp.com/api/user/" + post.authorId
      )
      .then((result) => {
        setAuthorName(result.data.name);
        setProfilePicture(result.data.profilePicture);
      })
      .catch((error) => console.log("ERROR in Post useEffect:", error));
  }, [post.authorId]);

  useEffect(() => {
    if (user) {
      if (user.postLiked.includes(post._id)) {
        setHasLiked(true);
      }
    }
  }, [user, post._id]);

  return (
    <div className="post-container">
      <Link to={`/post/${post._id}`} className="post-link">
        <h1 className="post-title">{post.title}</h1>
      </Link>

      <div className="post-details">
        <LikeSvg
          postId={post._id}
          hasLiked={hasLiked}
          setHasLiked={setHasLiked}
          setNumberOfLikes={setNumberOfLikes}
          numberOfLikes={numberOfLikes}
        />

        <h1 className="post-details-numberOfLikes">{numberOfLikes}</h1>
      </div>

      <div className="post-author-details">
        <img
          style={{ width: "50px", height: "50px", borderRadius: "50%" }}
          src={profilePicture}
          alt="avatar"
        />
        <h3 className="post-author">Made By {authorName}</h3>
      </div>
    </div>
  );
};

export default Post;
